  .ds-loader:before {
    position: absolute;
    bottom: -0.5em;
    animation: loader-icon-color 12s 0.33333s infinite backwards; }

@keyframes loader-icon-spin {
  0% {
    transform: rotate(0deg); }
  33%, 100% {
    transform: rotate(-360deg); } }

/* 
    Animation de changement de couleur de la forme Smile.

    step : 100% divis par le nombre de couleurs dans la liste donc chaque couleur reste affiche pendant la dure de l'animation / le nombre total de couleurs

    Pour chaque itration :
        index: l'index de la couleur en cours, dmarre  1
        color: la couleur correspondant  l'index

        {step * (index - 1)} = la position en %age dans l'animation de l'tape en cours
        {step * (index - 0.2)} = 20% avant la position de la prochaine tape
        Ces 2 chiffres permettent de bloquer la couleur entre ces deux valeurs

        Exemple, step 1 :
            0%, 13.33333% {
                color: #ff4b33;
            }
            La couleur est dfinie de 0% (step * 0) jusqu' 13.333% (step * 0.8)
        
        Exemple, step 2 & 3 :
            16.66667%, 30% {
                color: #f9bd00;
            }
            33.33333%, 46.66667% {
                color: #ff4b33;
            }
            La couleur est en jaune  partir de 16.66% (step * 1) jusqu' 30% (step * 1.8)
            De 30% (step * 1.8)  33.333% (step * 2) la couleur s'anime pour devenir corail
            Elle devient et reste corail de 33.3% (step * 2)  46.66% (step * 2.8)

*/
@keyframes loader-icon-color {
  0%, 13.33333% {
    color: #ff4b33; }
  16.66667%, 30% {
    color: #f9bd00; }
  33.33333%, 46.66667% {
    color: #ff4b33; }
  50%, 63.33333% {
    color: #1A283E; }
  66.66667%, 80% {
    color: #ff4b33; }
  83.33333%, 96.66667% {
    color: #f4b5d4; } }

